import React, { useState} from 'react';
import { BrowserRouter as Router, Switch, Route } from "react-router-dom"
import { makeStyles } from '@material-ui/core/styles';
import Drawer from '@material-ui/core/Drawer';
import AppBar from '@material-ui/core/AppBar';
import CssBaseline from '@material-ui/core/CssBaseline';
import Toolbar from '@material-ui/core/Toolbar';
import List from '@material-ui/core/List';
import Divider from '@material-ui/core/Divider';
import { mainListItems } from '../components/StudentListItems';
import CreateNewApplication from '../components/CreateNewApplication'
import NewApplicationButton from '../components/NewApplicationButton';
import ApplicationListingStudent from '../components/ApplicationListingStudent'
import applicationService from '../services/applications'
import Logo from '../components/logo.png';
import ExitToAppIcon from '@material-ui/icons/ExitToApp';
import Grid from '@material-ui/core/Grid';
import IconButton from '@material-ui/core/IconButton';
import Typography from '@material-ui/core/Typography';
import ViewListing from '../components/ViewListing';
import Tooltip from '@material-ui/core/Tooltip';
import SortByAlphaIcon from '@material-ui/icons/SortByAlpha';
import HistoryIcon from '@material-ui/icons/History';


const drawerWidth = 230;

const useStyles = makeStyles((theme) => ({
  root: {
    display: 'flex',
  },
  appBar: {
    zIndex: theme.zIndex.drawer + 1,
  },
  taButton: {
    marginRight: theme.spacing(0),
  },
  logoutButton: {
    marginLeft: 'auto',
    marginTop: theme.spacing(1),
  },
  drawer: {
    width: drawerWidth,
    flexShrink: 0,
  },
  drawerPaper: {
    width: drawerWidth,
  },
  drawerContainer: {
    overflow: 'auto',
  },
  content: {
    flexGrow: 1,
    padding: theme.spacing(3),
  },
}));

export default function PageApply({user, logout, modules, listings, listingToEdit, setListingToEdit, applications, setApplications}) {
  const classes = useStyles();

  const [byAlpha, setByAlpha] = useState(false)

  const toggleAlpha = () => {
    setByAlpha(!byAlpha)
  }

  const alphaCompare = (l1, l2) => {
    if (l1.module < l2.module) {
      return -1;
    }
    else if (l1.module > l2.module) {
      return 1;
    }
    return 0;
  }

  const addApplication = (applicationObject) => {
    applicationService
      .create(applicationObject)
      .then(returnedApplication => {
        setApplications(applications.concat(returnedApplication))
      })
  }

  const deleteApplication = (id) => {
    applicationService
      .destroy(id)
      .then(setApplications(applications.filter(application => {
          return application.id === id
        }))
      )
  } 


  const myApplications = byAlpha ? applications.filter(application => {
    if (user !== undefined) {
      return application.user.username === user.username;
    }
  }).sort(alphaCompare) : applications.filter(application => {
    if (user !== undefined) {
      return application.user.username === user.username;
    }
  }).reverse() 



  return (
    <div className={classes.root}>
      <CssBaseline />
        <AppBar position="fixed" className={classes.appBar}>
          <Toolbar>
            <Grid container spacing={3}>
              <Grid item xs={5}>
                <Typography variant="body2" noWrap>
                  <br></br>
                  {user !== undefined ? user.name : null} | {user !== undefined ? user.accountType : null}
                </Typography>
              </Grid>
              <Grid item xs={6}>
                <IconButton
                  justifyContent="center"
                  edge="center"
                  className={classes.taButton}
                  color="inherit"
                  href="/mymodules"
                >
                  <img src={Logo} />
                  <Typography variant="h6" noWrap>
                    &nbsp; Lent Connect
                  </Typography>
                </IconButton>
              </Grid>
              <Grid item xs={1}>
                <IconButton
                  edge="end"
                  color="inherit"
                  className={classes.logoutButton}
                  onClick={logout}
                >
                  <ExitToAppIcon style={{fill: "white"}}/>
                  <Typography variant="body2" noWrap>
                    &nbsp; Sign Out
                  </Typography>
                </IconButton>
              </Grid>
            </Grid>
        </Toolbar>
      </AppBar>
      <Drawer
        className={classes.drawer}
        variant="permanent"
        classes={{
          paper: classes.drawerPaper,
        }}
      >
        <Toolbar />
        <div className={classes.drawerContainer}>
        <List>{mainListItems}</List>
          <Divider />
        </div>
      </Drawer>
      <main className={classes.content}>
        <Toolbar />
        <Router>
            <Switch>
              <Route path="/apply/createnewapplication">
                <CreateNewApplication user={user} addApplication={addApplication} modules={modules} listings={listings} />
              </Route>
              <Route path="/mymodules/:moduleCode">
                <ViewListing user={user} listing={listingToEdit} setListingToEdit={setListingToEdit} />
              </Route>
              <Route path="/apply">
                <Grid container spacing={3} alignItems="center">
                  <Grid item xs={10}>
                    <NewApplicationButton />
                  </Grid>
                  <Grid item xs={2}>
                    {byAlpha ? (
                      <Tooltip title="Sort by Newest">
                      <IconButton
                        variant="outlined"
                        color="primary"
                        onClick={toggleAlpha}
                      >
                        <HistoryIcon />
                      </IconButton>
                    </Tooltip> ) : (
                      <Tooltip title="Sort by Alphabetical Order">
                      <IconButton
                        variant="outlined"
                        color="primary"
                        onClick={toggleAlpha}
                      >
                        <SortByAlphaIcon />
                      </IconButton>
                    </Tooltip>
                    )}
                  </Grid>
                  <Grid item xs={12}>
                  <Typography variant="h4" component="h2" align="left" >
                  <br></br>
                      My Applications
                  </Typography>
                  </Grid>
                  {myApplications.map((application, index) => (
                      <ApplicationListingStudent key={index} application={application} deleteApplication={deleteApplication} />
                    ))}
                </Grid>
              </Route>
              <Route path="/">
                <Grid container spacing={3} alignItems="center">
                  <Grid item xs={12}>
                    <NewApplicationButton />
                  </Grid>
                    {myApplications.map((application, index) => (
                      <ApplicationListingStudent key={index} application={application} deleteApplication={deleteApplication} />
                    ))}
                </Grid>
              </Route>
            </Switch>
          </Router>
      </main>
    </div>
  );
}